
// Libraries
import React from 'react';
import { Link } from 'react-router-dom';

// Styles
import styles from './style.module.scss';


const Footer = () => {
  return (
    <div className={styles.Footer}>
      <div className={styles.Wrapper}>
        <div className={styles.Cols}>
          <Link to='/'>
            <div className={styles.Logo}>TapShop</div>
          </Link>
          <div className={styles.Links}>
            <Link to='tariffs' className={styles.Link}>Тарифы</Link>
            <a href='http://themc.tilda.ws/offer' className={styles.Link}>Политика конфиденциальности</a>
            <a href='#' className={styles.Link}>Информация</a>
          </div>
        </div>
      </div>
      <div className={styles.Divider} />
      <div className={styles.Wrapper}>
        <div className={styles.Copyright}>Tapshop. Все права защищены</div>
      </div>
    </div>
  )
}

export default Footer;