
// Libraries
import React from 'react';
import {useMediaQuery} from 'react-responsive';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { v4 as uuid } from 'uuid';

// Components
import List from './List';
import BlockCreator from './BlockCreator';
import Page from '../Page';
import Header from '../Header';
import PhoneScreen from './PhoneScreen';
import Alert from '../Alert';

// Constants
import {
  DEFAULT_SETTINGS,
  SCREENS
} from './constants';
import {API} from '../../constants';

// Contexts
import {LoginScreenDataContext, LoaderDataContext} from '../../App.js';

// Assets
import arrowIcon from '../../assets/service/blackArrowToBottom.svg';
import arrowToRight from '../../assets/service/arrowToRight.svg';
import closeIcon from '../../assets/service/closeIcon.svg';
import deleteIcon from '../../assets/actionsIcons/delete.svg';
import addBlockIcon from '../../assets/blocksIcons/addBlock.svg';

// Styles
import 'react-sortable-tree/style.css';
import styles from './style.module.scss';


// For unregistered users
const DEFAULT_TEMPORARY_DATA = {
  id: 0,
  site_id: uuid(),
  name: 'Главная',
  link: '/',
  settings: DEFAULT_SETTINGS,
  blocks: [
    {
      id: uuid(),
      type_block_id: 1,
      page_id: 0,
      index_number: 0,
      content: {
        text: '<p>Текст1</p>',
        settings: {
          textStyle: 'large',
          alignment: 'left',
          font: 'Arial'
        }
      }
    },
    {
      id: uuid(),
      type_block_id: 4,
      index_number: 1,
      page_id: 0,
      content: {
        vk: 'vk'
      }
    },
    {
      id: uuid(),
      type_block_id: 1,
      index_number: 2,
      page_id: 0,
      content: {
        text: '<p>Текст2</p>',
        settings: {
          textStyle: 'large',
          alignment: 'left',
          font: 'Arial'
        }
      }
    },
  ],
  warnings: [/*
    {
      type: 'subscription',
      date: moment(),
      currentSubType: 'PRO'
    }
  */]
};

export const PhoneScreenDataContext = React.createContext();
export const AlertScreenDataContext = React.createContext();

const  Editor = () => {
  const {
    userData: {
      user,
      token
    },
    editorPages: pages
  } = useSelector(store => store);
  const dispatch = useDispatch();
  const isMobile = useMediaQuery({ query: '(max-width: 991px)' });
  const { setLoginData } = React.useContext(LoginScreenDataContext);
  const { setLoaderData } = React.useContext(LoaderDataContext);

  const changePage = page => dispatch({ type: 'EDITOR_CHANGE_PAGE', page });

  // STATES
  const [activePage, setActivePage] = React.useState((pages && pages[0]) || null);
  const [phoneScreenData, setPhoneScreenData] = React.useState(null);
  const [alertData, setAlertData] = React.useState(null);
  const [selectedBlock, setSelectedBlock] = React.useState(null);
  const [creatingBlock, setCreatingBlock] = React.useState(null);
  const [screen, setScreen] = React.useState(null);

  const providedPhone = React.useMemo(() => ({
    phoneScreenData,
    setPhoneScreenData
  }), [phoneScreenData]);

  const providedAlert = React.useMemo(() => ({
    alertData,
    setAlertData
  }), [alertData]);

  const queryWrapper = async (query) => {
    setLoaderData({ loading: true });
    await query();
    setLoaderData({ loading: false });
  };

  const fetchPage = id => {
    queryWrapper(
      () => fetch(`${API}/api/page/${id || activePage?.id}`, {
        method: 'GET',
        headers: { Authorization: `Bearer ${token}` }
      })
        .then(res => res.json())
        .then(page => {
          setActivePage(page)
        })
    )
  }

  const blockDoWrapper = async (query) => {
    await queryWrapper(query);
    await fetchPage();
  };

  const createPage = (name, link) => {
    fetch(`${API}/api/page`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          name,
          link,
          settings: DEFAULT_SETTINGS
        })
    })
      .then(res => res.json())
      .then(res => {
        dispatch({
          type: 'EDITOR_ADD_PAGE',
          page: res
        });
        setActivePage(res);
        setSelectedBlock(activePage.blocks[activePage.blocks.length - 1]);
      })
      .catch(err => console.log('err', err))
  };

  const createBlock = (type_block_id, content) => {
    const dataForCreate = {
      type_block_id,
      page_id: activePage.id,
      index_number: activePage.blocks.length,
      content
    }

    if (user) {
      blockDoWrapper(
        () => fetch(`${API}/api/block`, {
          method: 'POST',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(dataForCreate)
        })
          .then(() => setSelectedBlock(activePage.blocks[activePage.blocks.length - 1]))
          .catch(err => console.log('err', err))
      );
    } else {
      const customBlock = {
        id: uuid(),
        ...dataForCreate
      }
      const updatedPage = {
        ...activePage,
        blocks: [
          ...activePage.blocks,
          customBlock
        ]
      }

      changePage(updatedPage);
      setSelectedBlock(customBlock);
    }
  };

  const updateBlock = (content, blockFrom) => {
    const usedBlock = selectedBlock || blockFrom;

    const dataForUpdate = {
      id: usedBlock.id,
      type_block_id: usedBlock.type_block_id,
      page_id: activePage.id,
      index_number: usedBlock.index_number,
      content
    }

    if (user) {
      blockDoWrapper(
        () => fetch(`${API}/api/block/${usedBlock.id}`, {
          method: 'PUT',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(dataForUpdate)
        })
          .then(() => setSelectedBlock(dataForUpdate))
          .catch(err => console.log('err', err))
      )
    } else {
      const editedBlocks = [...activePage.blocks];
      const foundIndex = editedBlocks.findIndex(({ id }) => id === selectedBlock.id);
      editedBlocks[foundIndex] = dataForUpdate;
      const updatedPage = {
        ...activePage,
        blocks: editedBlocks
      };
      setSelectedBlock(dataForUpdate);
      changePage(updatedPage);
    }
  };

  const deleteBlock = id => {
    if (user) {
      blockDoWrapper(
        () => fetch(`${API}/api/block/${id}`, {
          method: 'DELETE',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
        })
          .then(res => console.log('res,', res))
          .catch(err => console.log('err', err))
      );
    } else {
      const updatedPage = {
        ...activePage,
        blocks: activePage.blocks.filter(({ id: blockId }) => blockId !== id)
      }

      changePage(updatedPage);
    }
  };

  const swapBlocks = (first_id, second_id) => {
    const doSwapOnUserSide = () => {
      const editedBlocks = [...activePage.blocks];
      const block1Index = editedBlocks.findIndex(({ id }) => first_id === id);
      const block2Index = editedBlocks.findIndex(({ id }) => second_id === id);
      editedBlocks[block1Index] = {
        ...activePage.blocks[block1Index],
        index_number: activePage.blocks[block2Index].index_number
      };
      editedBlocks[block2Index] = {
        ...activePage.blocks[block2Index],
        index_number: activePage.blocks[block1Index].index_number
      };
      setSelectedBlock(editedBlocks[block1Index]);
      const updatedPage = {
        ...activePage,
        blocks: editedBlocks
      }

      changePage(updatedPage);
    };

    if (user) {
      blockDoWrapper(
        () => fetch(`${API}/api/block-index-change`, {
          method: 'POST',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            first_id,
            second_id,
          })
        })
          .then(() => doSwapOnUserSide())
          .catch(err => console.log('err', err))
      );
    } else {
      doSwapOnUserSide();
    }
  };

  const deletePage = () => {
    if (user) {
      fetch(`${API}/api/page/${activePage.id}`, {
          method: 'DELETE',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
        })
          .then(res => console.log('res', res))
          .catch(err => console.log('err', err))
    }

    dispatch({ type: 'EDITOR_REMOVE_PAGE', page: activePage });

    const activePageIndex = pages.findIndex(({ id }) => id === activePage.id);
    setActivePage(activePageIndex === 0 ? pages[1] : pages[0]);
  }

  const handleChangeSettings = (settings) => {
    const updatedPage = {
      ...activePage,
      settings
    };

    fetch(`${API}/api/page/${activePage.id}`, {
      method: 'PUT',
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(updatedPage)
    })
      .then(() => {
        changePage(updatedPage);
        setActivePage(updatedPage);
      })
      .catch(err => console.log('err', err))
  };

  const handleClear = () => {
    setCreatingBlock(null);
  };

  const actions = {
    setAlertData,
    setActivePage,
    setSelectedBlock,
    setScreen,
    setCreatingBlock,
    setLoginData,
    setPhoneScreenData,

    createBlock,
    updateBlock,
    deleteBlock,
    swapBlocks,
    changePage,
    blockDoWrapper,

    dispatch
  };

  const values = {
    alertData,
    pages,
    activePage,
    selectedBlock,
    screen,
    creatingBlock,
    user
  };

  React.useEffect(() => {
    if (!activePage?.blocks && user) {
      fetchPage(activePage?.id)
    }
  }, [activePage]);

  const getPages = () => {
    if (user) {
      fetch(`${API}/api/list-pages`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${token}`,
        }
      })
        .then(res => res.json())
        .then(pages => {
          if (pages.length) {
            dispatch({
              type: 'EDITOR_SET_PAGES',
              pages,
            });
            setActivePage(pages[0]);
          } else {
            blockDoWrapper(() => fetch(`${API}/api/site`, {
              method: 'POST',
              headers: {
                'Authorization': `Bearer ${token}`,
                'Content-Type': 'application/json',
                'Accept': 'application/json, text/plain, */*'
              },
              body: JSON.stringify({
                blocks: activePage?.blocks
                  ? activePage.blocks.map(block => ({
                    type_block_id: block.type_block_id,
                    content: block.content
                  }))
                  : DEFAULT_TEMPORARY_DATA.blocks
                ,
                name: activePage?.name || DEFAULT_TEMPORARY_DATA.name,
                link: activePage?.link || DEFAULT_TEMPORARY_DATA.link,
                settings: activePage?.settings || DEFAULT_TEMPORARY_DATA.settings
              })
            })
              .then(res => res.json())
              .then(res => fetchPage(res.pages[0].id))
            )
          }
        })
    }
  }

  React.useEffect(() => {
    if (user) {
      getPages();
    }
  }, [user]);

  React.useEffect(() => {
    if (!user) {
      setActivePage(prev => prev ? pages.find(({ id }) => id === prev.id) : pages[0]);
    }
  }, [pages]);

  React.useEffect(() => {
    if (creatingBlock) {
      setScreen(null);
    }
  }, [creatingBlock]);

  React.useEffect(() => {
    pages.length === 0 && user && dispatch({ type: 'EDITOR_ADD_PAGE', page: DEFAULT_TEMPORARY_DATA })
  }, []);

  const headerProps = {
    menu: isMobile ? 'default' : 'empty',
    renderTitle: () => (
      <div
        onClick={() => !creatingBlock && setScreen(SCREENS.showAllPages)}
        style={{
          cursor: !creatingBlock && 'pointer',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          fontWeight: 500
        }}
      >
        {creatingBlock
          ? creatingBlock.title
          : (
            activePage && (
              <>
                <span className='limited'>
                  {activePage?.name}
                </span>
                <img
                  src={arrowIcon}
                  style={{
                    transform: screen && 'rotate(180deg)',
                    marginLeft: 5
                  }}
                  alt='Вниз'
                />
              </>
            )
          )}
      </div>
    ),
    renderActionButton: () => (
      <div onClick={() => setCreatingBlock(null)} style={{display: 'flex'}}>
        {creatingBlock
          ? <img src={closeIcon} alt='Закрыть' />
          : !user
            ? isMobile
              && (
                <span onClick={() => setLoginData({ show: true })}>
                  Войти
                </span>
              )
            : (
              <img
                src={deleteIcon}
                onClick={() => pages.length > 1 && deletePage()}
                style={{cursor: pages.length > 1 ? 'pointer' : '', opacity: pages.length > 1 ? 1 : 0.3}}
                alt='Удалить'
              />
            )
        }
      </div>
    )
  };

  React.useEffect(() => {
    setPhoneScreenData({
      isShow: screen,
      setHide: () => setScreen(null),
      actions,
      values,
      ...screen
    })
  }, [screen]);

  return (
    <PhoneScreenDataContext.Provider value={providedPhone}>
      <AlertScreenDataContext.Provider value={providedAlert}>
        <Page
          hideHeader={!isMobile || !!screen}
          contentStyles={{
            background: activePage?.settings?.background.image
              ? `url(${activePage?.settings?.background.image }) no-repeat center / cover`
              : activePage?.settings?.color,
            height: creatingBlock && 'inherit'
          }}
        >
          {!isMobile && (
            <div className={`${styles.Wrapper} ${styles.GetLinkPC}`}>
              {user?.login_instagram ? (
                <span style={{display: 'flex', alignItems: 'center'}}>
                  <span>Моя ссылка:&nbsp;</span>
                  <Link to={`/${user.login_instagram}`} style={{color: '#6442EE'}}>
                    <div className={styles.GetLink__Link}>/{user.login_instagram}</div>
                  </Link>
                </span>
              ) : (
                <>
                  <span>
                    Для получения ссылки нажмите кнопку «Создать сайт»
                  </span>
                  <button className={styles.GetLinkPC__Button} onClick={() => setLoginData({ show: true, step: 'ConnectWithInstagram' })}>
                    Создать сайт
                  </button>
                </>
              )}
            </div>
          )}
          <div className={styles.Editor}>
            {<PhoneScreen {...phoneScreenData} />}
            {alertData && <Alert alertData={alertData} setHide={() => setAlertData(null)} />}
            <Header {...headerProps} />
            <div className={styles.Contain}>
              {!creatingBlock && isMobile && (
                <div className={styles.GetLink}>
                  {user?.login_instagram ? (
                    <Link to={`/${user.login_instagram}`}>
                      <div className={styles.GetLink__Link}>/{user.login_instagram}</div>
                    </Link>
                  ) : (
                    <>
                      <div className={styles.GetLink__Help}>
                        Получить ссылку
                      </div>
                      <div className={styles.GetLink__Arrow}>
                        <img src={arrowToRight} alt='Вправо' />
                      </div>
                      <button className={styles.GetLink__Button} onClick={() => setLoginData({ show: true, step: 'ConnectWithInstagram' })}>
                        Создать сайт
                      </button>
                    </>
                  )}
                </div>
              )}
              {activePage
                ? <>
                  {creatingBlock ? (
                    <BlockCreator
                      defaultFormData={creatingBlock.isSettings ? activePage.settings : selectedBlock?.content}
                      creatingBlock={creatingBlock}
                      activePage={activePage}
                      handleBlockSave={content => {
                        switch (creatingBlock?.event) {
                          case 'isAddPage': {
                            if (user) {
                              createPage(content.title, content.url);
                            } else {
                              dispatch({
                                type: 'EDITOR_ADD_PAGE',
                                page: {
                                  id: uuid(),
                                  blocks: [],
                                  name: content.title,
                                  link: content.url,
                                  settings: DEFAULT_SETTINGS
                                }
                              });
                            }
                            handleClear();
                            return;
                          }

                          case 'isSettings': {
                            handleChangeSettings(content);
                            handleClear();
                            return;
                          }

                          case 'blockLikeSettings': {
                            const foundIndex = activePage.blocks.findIndex(prevBlock => prevBlock.type_block_id === creatingBlock.type_block_id);

                            if (foundIndex > -1) {
                              updateBlock(content, activePage.blocks[foundIndex]);
                            } else {
                              createBlock(creatingBlock.type_block_id, content);
                            }
                            handleClear();
                            return;
                          }

                          default: {
                            const foundIndex = activePage.blocks.findIndex(prevBlock => prevBlock.id === selectedBlock?.id);
                            if (foundIndex > -1) {
                              updateBlock(content);
                            } else {
                              createBlock(creatingBlock.type_block_id, content);
                            }
                            handleClear();
                          }
                        }
                      }}
                    />
                  ) : (
                    <List
                      actions={actions}
                      values={values}
                    />
                  )}
                  {(!screen && !creatingBlock) && (
                    <div
                      className={styles.Add}
                      onClick={() => {
                        setSelectedBlock(null);
                        setScreen(SCREENS.addBlock);
                      }}
                    >
                      <img src={addBlockIcon} alt='Добавить блок' />
                    </div>
                  )}
                </>
                : pages.length === 0 && !user
                && (
                  <div>
                    <div style={{margin: '10px', padding: '10px', background: '#fff', textAlign: 'center', borderRadius: 8}}>
                      Войдите в <span style={{cursor: 'pointer', color: '#6442EE'}} onClick={() => setLoginData({ show: true })}>аккаунт</span>
                      <div style={{margin: '5px 0', color: '#9692A5', fontSize: 14}}>или</div>
                      Кликните <span style={{cursor: 'pointer', color: '#6442EE'}} onClick={() => dispatch({ type: 'EDITOR_ADD_PAGE', page: DEFAULT_TEMPORARY_DATA })}>сюда</span> чтобы создать страницу и базовый набор блоков!
                    </div>
                  </div>
                )
              }
            </div>
          </div>
        </Page>
      </AlertScreenDataContext.Provider>
    </PhoneScreenDataContext.Provider>
  )
}

export default Editor;